<template>
    <div class="wrapper" ref="wrapper">
      <slot></slot>
    </div>
</template>

<script>
  import Bscroll from 'better-scroll'
    export default {
      name: "scroller",
      props:{
        handleToScroll:{
          type:Function,
          default:function(){}
        },
        handleToTouchEnd:{
          type:Function,
          default:function(){}
        }
      },
      mounted(){
          var scroll=new Bscroll(this.$refs.wrapper,{
            tap:true,
           click: true, //better-scroll  click  truebetter-scroll  click  event  _constructed true
            probeType: 2, //
            mouseWheel: true
          });
          this.scroll=scroll;
          scroll.on('scroll',(pos)=>{
            this.handleToScroll(pos)
          });
          scroll.on('touchEnd',(pos)=>{
            this.handleToTouchEnd(pos)
          })
          
      },
      methods:{
        toScrollTop(y){
            this.scroll.scrollTo(0,y)
        }
      }
    }
</script>

<style scoped lang="less">
  .wrapper{
    height:100%;
  }
</style>



