<template>
  <div class="login">
    <h2></h2>
    <!--  -->
    <InputGroup
      type="number"
      v-model="phone"
      placeholder=""
      :btnTitle="btnTitle"
      :disabled="disabled"
      :error="errors.phone"
      :fontclass="fontclass"
      @btnClick="getVerifyCode"
    />

    <!--  -->
    <InputGroup type="number" v-model="verifyCode" placeholder="" :error="errors.code" :fontclass="lock"/>
    <!--  -->
    <InputGroup type="text" v-model="passwd1" placeholder="6-12" :error="errors.passwd1"
                :fontclass="lock"/>
    <!--  -->
    <InputGroup type="text" v-model="passwd2" placeholder="" :error="errors.passwd2" :fontclass="lock"/>
    <!--  -->
    <div class="login_btn">
      <button @click="handleLogin"></button>
    </div>
    <p v-show="successflag"></p>
  </div>
</template>

<script>
    import InputGroup from "@/components/InputGroup";

    export default {
        name: "login",
        data() {
            return {
                phone: "",
                verifyCode: "",
                passwd1: "",
                passwd2: "",
                errors: {},
                btnTitle: "",
                disabled: false,
                fontclass: 'fa fa-mobile',
                lock: "fa fa-codepen",
                successflag:false
            };
        },
        computed: {},
        methods: {
            // 
            handleLogin() {
                // 
                if (!this.validatePhone() || !this.validatePassword()) {
                    return false;
                }
                this.axios.post("/api/api/user/Cpasswd", {
                    phone: this.phone,
                    code: this.verifyCode,
                    passwd1: this.passwd1,
                    passwd2: this.passwd2
                }).then(res => {
                        console.log(res)
                        if (res.data.statu == 200) {
                        this.successflag=true;
                            //
                            setTimeout(()=>{
                                this.$router.push("/login");
                            },1000)
                        } else {
                            this.errors = {
                                passwd2: res.data.msg
                            };
                        }
                    })
                    .catch(err => {
                        // 
                    });
            },
            getVerifyCode() {
                if (this.validatePhone()) {
                    this.validateBtn();
                    // 
                    this.axios
                        .post("/api/api/user/send_SMS", {
                            phone: this.phone
                        })
                        .then(res => {
                            console.log(res);
                        })
                }
            },
            validateBtn() {
                let time = 60;
                let timer = setInterval(() => {
                    if (time == 0) {
                        clearInterval(timer);
                        this.btnTitle = "";
                        this.disabled = false;
                    } else {
                        // 
                        this.btnTitle = time + "";
                        this.disabled = true;
                        time--;
                    }
                }, 1000);
            },
            // 
            validatePhone() {
                if (!this.phone) {
                    this.errors = {
                        phone: ""
                    };
                    return false;
                } else if (!/^1[345678]\d{9}$/.test(this.phone)) {
                    this.errors = {
                        phone: ""
                    };
                    return false;
                } else {
                    this.errors = {};
                    return true;
                }
            },
            // 
            validatePassword() {
                if (!this.passwd1) {
                    this.errors = {
                        passwd2: ""
                    };
                    return false;
                } else if (!/^[\w]{6,12}$/.test(this.password)) {
                    this.errors = {
                        passwd2: "6-12"
                    };
                    return false;
                } else {
                    this.errors = {};
                    return true;
                }
            }
        },
        components: {
            InputGroup
        }
    };
</script>

<style scoped>
  .login {
    padding-top: 10vh;
    padding: 10vh 0.6rem 0;
  }
  .login h2 {
    line-height: 0.6rem;
  }

  .text_group,
  .login_des,
  .login_btn {
    margin-top: 0.58rem;
  }

  .login_btn {
    margin-top: 2rem;
  }

  .login_des {
    color: #aaa;
    line-height: 22px;
  }

  .login_des span {
    color: #4d90fe;
  }

  .login_btn button {
    width: 6.3rem;
    height: 0.9rem;
    background-color: #369836;
    border-radius: 0.6rem;
    color: #fff;
    font-size: 0.3rem;
    border: none;
    outline: none;
  }

  .login_btn span {
    font-size: 0.3rem;
    color: #666666;
    margin-top: 0.1rem;
    display: block;
    padding-left: 0.2rem;
  }

  .login_btn button[disabled] {
    background-color: #8bda81;
  }
  .login>P{
    width:3rem;
    background:rgba(0,0,0,0.6);
    height:0.8rem;
    line-height:0.8rem;
    border-radius:0.1rem;
    color:#fff;
    position: fixed;
    top:50%;
    left:50%;
    margin-top:-0.4rem;
    margin-left:-1.5rem;
    text-align: center;
  }
</style>



