<template>
  <div class="login">
    <router-link tag="p" to="/register" class="register">
      <span></span>
    </router-link>
    <h2></h2>
    <template v-if="flag">
      <InputGroup
        type="text"
        v-model="user"
        placeholder=""
        :error="errors.phone"
        :fontclass="fontclass"
      />
      <!--  -->
      <InputGroup
        type="password"
        v-model="password"
        placeholder=""
        :error="errors.password"
        :fontclass="lock"
      />
    </template>
    <template v-else>
      <!--  -->
      <InputGroup
        type="number"
        v-model="phone"
        placeholder=""
        :btnTitle="btnTitle"
        :disabled="disabled"
        :error="errors.phone"
        :fontclass="fontclass"
        @btnClick="getVerifyCode"
      />
      <!--  -->
      <InputGroup
        type="number"
        v-model="verifyCode"
        placeholder=""
        :error="errors.code"
        :fontclass="lock"
      />
    </template>
    <!--  -->
    <router-link tag="p" to="/backpassword" class="forget">?</router-link>
    <!--  -->
    <div class="login_btn">
      <button @click="handleLogin"></button>
      <span v-if="flag" @click="flag=false"></span>
      <span v-else @click="flag=true"></span>
    </div>
  </div>
</template>

<script>
import InputGroup from "@/components/InputGroup";
export default {
  name: "login",
  data() {
    return {
      flag: true,
      user: "", //
      phone: "", //
      password: "", //
      errors: {}, //
      verifyCode: "", //
      btnTitle: "",
      disabled: false,
      fontclass: "fa fa-user-o",
      lock: "fa fa-unlock-alt",
      btnTitle: ""
    };
  },
  computed: {},
  methods: {
    // 
    handleLogin() {
        if(this.flag){
            this.axios
                .post("/api/api/user/login", {
                    phone: this.user,
                    passwd:this.password
                })
                .then(res => {
                    if (res.data.statu == 200) {
                        //
                        // 
                        // localStorage.setItem("ele_login", true);
                         this.$router.go(-1);
                    } else {
                        this.errors = {
                            password: res.data.msg
                        };
                    }
                })
                .catch(err => {
                    // 
                });
        }else{
            this.axios
                .post("/api/api/user/Ylogin", {
                    phone: this.phone,
                    code:this.verifyCode
                })
                .then(res => {
                    if (res.data.statu == 200) {
                         // 
                        localStorage.setItem("ele_login", true);
                        this.$router.push("/home");
                    } else {
                        this.errors = {
                            code: res.data.msg
                        };
                    }
                })
                .catch(err => {
                    // 
                });
        }

    },
      getVerifyCode() {
          if (this.validatePhone()) {
              this.validateBtn();
              // 
              this.axios
                  .post("/api/api/user/send_SMS", {
                      phone: this.phone
                  })
                  .then(res => {
                      console.log(res);
                  })
          }
      },
      validateBtn() {
          let time = 60;
          let timer = setInterval(() => {
              if (time == 0) {
                  clearInterval(timer);
                  this.btnTitle = "";
                  this.disabled = false;
              } else {
                  // 
                  this.btnTitle = time + "";
                  this.disabled = true;
                  time--;
              }
          }, 1000);
      },
      // 
      validatePhone() {
          if (!this.phone) {
              this.errors = {
                  phone:""
              };
              return false;
          } else if (!/^1[345678]\d{9}$/.test(this.phone)) {
              this.errors = {
                  phone: ""
              };
              return false;
          } else {
              this.errors = {};
              return true;
          }
      },
  },
  components: {
    InputGroup
  }
};
</script>

<style scoped>
.login {
  padding-top: 10vh;
  padding: 5vh 0.6rem 0;
}
.forget,
.register {
  font-size: 0.26rem;
  color: #666666;
  line-height: 0.6rem;
  width: 1.4rem;
  float: right;
}
.register {
  font-size: 0.3rem;
  color: #369836;
  width: 100%;
  text-align: right;
  letter-spacing: 0.1rem;
  margin-bottom: 0.3rem;
}
.register span {
  padding: 0.2rem 0.2rem;
}
.login h2 {
  line-height: 0.6rem;
}
.text_group,
.login_des,
.login_btn {
  margin-top: 0.58rem;
}
.login_btn {
  margin-top: 2rem;
}

.login_des {
  color: #aaa;
  line-height: 22px;
}
.login_des span {
  color: #4d90fe;
}
.login_btn button {
  width: 6.3rem;
  height: 0.9rem;
  background-color: #369836;
  border-radius: 0.6rem;
  color: #fff;
  font-size: 0.3rem;
  border: none;
  outline: none;
}
.login_btn span {
  font-size: 0.3rem;
  color: #666666;
  margin-top: 0.1rem;
  display: block;
  padding-left: 0.2rem;
}
.login_btn button[disabled] {
  background-color: #8bda81;
}
</style>



